// =============================================================================
// DATABASE CLIENT FACTORY
// =============================================================================
// Automatically selects SQLite (dev) or PostgreSQL (prod) based on environment

import type { DatabaseAdapter } from './adapters/base-adapter';
import { SqliteAdapter } from './adapters/sqlite-adapter';
import { PostgresAdapter } from './adapters/postgres-adapter';

let dbAdapter: DatabaseAdapter | null = null;

/**
 * Get the database adapter instance
 * Uses PostgreSQL if DATABASE_URL is set, otherwise falls back to SQLite
 */
export function getDb(): DatabaseAdapter {
  if (dbAdapter) {
    return dbAdapter;
  }

  const databaseUrl = process.env.DATABASE_URL;

  if (databaseUrl && databaseUrl.startsWith('postgresql://')) {
    // Production: Use PostgreSQL
    console.log('[db] Using PostgreSQL adapter');
    dbAdapter = new PostgresAdapter(databaseUrl);
  } else {
    // Development: Use SQLite
    console.log('[db] Using SQLite adapter (development mode)');
    dbAdapter = new SqliteAdapter();
  }

  return dbAdapter;
}

/**
 * Close database connections (useful for cleanup in tests)
 */
export async function closeDb(): Promise<void> {
  if (dbAdapter) {
    if ('close' in dbAdapter && typeof dbAdapter.close === 'function') {
      await dbAdapter.close();
    }
    dbAdapter = null;
  }
}

