// =============================================================================
// SMF Health API
// =============================================================================
// GET /api/v1/smf/health
// Returns summary health status for all facilitators

import { NextResponse } from 'next/server';
import { getLatestHealthSnapshot } from '@/db/smf';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'SMFHealthAPI' });

export async function GET() {
  try {
    // Get facilitators from database instead of router (avoids loading CDP SDK)
    let facilitators: Array<{ id: string; name: string }> = [];
    try {
      const { getDb } = await import('@/db/client');
      const db = getDb();
      const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
      
      if (isPostgres) {
        const result = await (db as any).pool.query('SELECT id, name, status FROM facilitators WHERE status = $1', ['active']);
        facilitators = result.rows;
      } else {
        const stmt = (db as any).db.prepare('SELECT id, name, status FROM facilitators WHERE status = ?');
        facilitators = stmt.all('active');
      }
    } catch (error) {
      logger.warn({ error }, 'Failed to load facilitators from database, using defaults');
      // Fallback to default CDP facilitator
      facilitators = [{ id: 'cdp', name: 'Coinbase Developer Platform' }];
    }

    const healthStatus = await Promise.all(
      facilitators.map(async (facilitator) => {
        try {
          // Get latest health snapshot
          const snapshot = await getLatestHealthSnapshot(
            facilitator.id,
            'base', // Default network - can be made configurable
            '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913' // Default token - can be made configurable
          );

          // Determine overall status based on snapshot
          let status: 'healthy' | 'degraded' | 'down' = 'healthy';
          let reasons: string[] = [];

          if (snapshot) {
            status = snapshot.status;
            if (snapshot.success_rate < 0.5) {
              reasons.push('low-success-rate');
            }
            if (snapshot.p95_latency_ms && snapshot.p95_latency_ms > 5000) {
              reasons.push('high-latency');
            }
            if (snapshot.error_rate > 0.1) {
              reasons.push('high-error-rate');
            }
          } else {
            // No snapshot data - assume healthy but mark as unknown
            status = 'healthy';
            reasons.push('no-data');
          }

          return {
            facilitatorId: facilitator.id,
            name: facilitator.name,
            status,
            reasons: reasons.length > 0 ? reasons : undefined,
            metrics: snapshot
              ? {
                  successRate: Number(snapshot.success_rate),
                  p95LatencyMs: snapshot.p95_latency_ms,
                  errorRate: Number(snapshot.error_rate),
                  lastErrorType: snapshot.last_error_type,
                  lastUpdated: snapshot.window_end,
                }
              : undefined,
          };
        } catch (error) {
          logger.error({ error, facilitatorId: facilitator.id }, 'Failed to get health status');
          return {
            facilitatorId: facilitator.id,
            name: facilitator.name,
            status: 'down' as const,
            reasons: ['health-check-error'],
          };
        }
      })
    );

    // Determine overall system status
    const healthyCount = healthStatus.filter((h) => h.status === 'healthy').length;
    const degradedCount = healthStatus.filter((h) => h.status === 'degraded').length;
    const downCount = healthStatus.filter((h) => h.status === 'down').length;

    let systemStatus: 'healthy' | 'degraded' | 'down' = 'healthy';
    if (downCount > 0) {
      systemStatus = 'down';
    } else if (degradedCount > 0) {
      systemStatus = 'degraded';
    }

    return NextResponse.json({
      status: systemStatus,
      summary: {
        total: facilitators.length,
        healthy: healthyCount,
        degraded: degradedCount,
        down: downCount,
      },
      facilitators: healthStatus,
      timestamp: new Date().toISOString(),
    });
  } catch (error) {
    logger.error({ error }, 'Failed to get SMF health');
    return NextResponse.json(
      {
        status: 'down',
        error: error instanceof Error ? error.message : 'Unknown error',
        timestamp: new Date().toISOString(),
      },
      { status: 500 }
    );
  }
}

